<template>
    <div class="item-import">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">Home</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/project' }">Project Management</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name:'Item',params:{id:$route.params.id} }"> Translation Item
            </el-breadcrumb-item>
            <el-breadcrumb-item>Import</el-breadcrumb-item>
        </el-breadcrumb>
        <br>
        <el-card shadow="never">
            <el-upload
                    drag
                    :limit="1"
                    :headers="headers"
                    style="margin-left: 10px;display: flex;
    align-items: center;
    justify-content: center;
    margin-top: 60px;"
                    :action="actionUrl"
                    :show-file-list="false"
                    :on-success="onSuccess"
                    :before-upload="beforeExcelUpload">
                <i class="el-icon-upload"></i>
                <div class="el-upload__text">将文件拖到此处，或<em>点击上传</em></div>
            </el-upload>
            <br>

            <h2 style="text-align:center" v-if="uploadSuccess">上传成功</h2>
            <h3 style="text-align: center">导入速度较慢，请耐心等待</h3>

            <div style="padding: 20px">
                <p>上传文件要求:</p>
                <ul style="background: #f3f3f3;padding: 10px;list-style: none;margin: 0;color: #777777;font-size: 13px;line-height: 1.7;border-radius: 4px">
                    <li>1. CSV 文件内容不能带 `\n` 特殊字符，因为`\n` 是CSV的默认行分割符。</li>
                    <li>2. Excel 最好使用 Microsoft Excel 2007/2010 Workbook (*.xlsx)。</li>
                    <li>3. Excel 不要带任何格式，避免解析错误。</li>
                    <li>4. Excel 工作表名必须统一为 <em style="color: #5daf34;font-style: normal;font-weight: bold">Sheet1</em></li>
                    <li>5. 第一行除了第一列名称为KEY之外，其他列如果是对应的语言词条翻译的，必须指定语言，例如简体中文：zh-cn</li>
                </ul>
            </div>
            <div>示例：(暂不支持导入描述，标签功能)</div>

            <img :src="excelExample" alt="">
        </el-card>
    </div>
</template>

<script>
    import VueCookies from "vue-cookies";
    import {Message} from "element-ui";

    export default {
        name: "ItemImport",
        data() {
            return {
                actionUrl: "",
                uploadSuccess: false,
                headers: {
                    "Authorization": ""
                },
                excelExample: require('../../assets/excel.jpg')
            }
        },
        created() {
            this.headers.Authorization = VueCookies.get("token");
            this.actionUrl = `${process.env.VUE_APP_API_URL}/api/v1/projects/${this.$route.params.id}/upload`;
        },
        methods: {
            beforeExcelUpload(file) {
                console.log(file.type);
                const isExcel = file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || 'text/csv';

                if (!isExcel) {
                    Message.error('上传文件只能是 Excel 或者 Csv 格式!');
                }

                return isExcel
            },
            onSuccess(response, file, fileList) {
                //this.uploading = false;
                //setTimeout(()=>this.fetchStream(),1000)
                this.uploadSuccess = true
            },
        }
    }
</script>

