<template>
    <div class="translator">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">{{$t('home')}}</el-breadcrumb-item>
            <el-breadcrumb-item>{{$t('translator')}}</el-breadcrumb-item>
        </el-breadcrumb>
        <br>

        <el-card shadow="never">
            <el-table :data="users">
                <el-table-column label="ID" prop="user_id"></el-table-column>
                <el-table-column :label="$t('username')" prop="username"></el-table-column>
                <el-table-column :label="$t('nickname')" prop="nickname"></el-table-column>
                <el-table-column label="Email" prop="email"></el-table-column>
                <el-table-column :label="$t('is_admin')" prop="is_admin">
                    <template slot-scope="scope">
                        <el-switch
                                v-model="scope.row.is_admin"
                                active-color="#13ce66"
                                inactive-color="#ff4949"
                                @change="changeHandle(scope.row.user_id,$event)"
                                active-value="YES"
                                :disabled="'NO' === $root.isAdmin || scope.row.username === 'root'"
                                inactive-value="NO">
                        </el-switch>
                    </template>
                </el-table-column>
            </el-table>
            <br>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    @current-change="currentChangeHandle"
                    :total="count">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "Translator",
        data() {
            return {
                users: [],
                count: 0,
            }
        },
        created() {
            this.fetchUser();
        },
        methods: {
            currentChangeHandle(page) {
                this.fetchUser({page: page})
            },
            async changeHandle(id, val) {
                await axios.put("/api/v1/users/" + id, {is_admin: val})
            },
            async fetchUser(params = {}) {
                let {data} = await axios.get("/api/v1/users", {params: params});
                this.users = data.data.data;
                this.count = data.data.paging.count || 0;
            }
        }

    }
</script>

