<template>
    <div class="language">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">Home</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/project' }">Project Management</el-breadcrumb-item>
            <el-breadcrumb-item> Language Management</el-breadcrumb-item>
        </el-breadcrumb>
        <br>

        <el-card shadow="never">
            <el-row :gutter="15" type="flex" justify="space-between">
                <el-col :span="16" style="flex: 1">
                    <el-button type="primary" @click="addVisible = true">Add</el-button>
                </el-col>
                <el-col :span="6">
                    <el-input placeholder="请输入内容" size="small" class="input-with-select">
                        <el-button slot="append" icon="el-icon-search"/>
                    </el-input>
                </el-col>
            </el-row>

            <el-table :data="tableData" style="width: 100%">
                <el-table-column prop="language" label="Language Name">
                </el-table-column>
                <el-table-column prop="code" label="Language Code">
                </el-table-column>
                <el-table-column prop="translators" label="Translator">
                    <template slot-scope="scope">
                        <span v-if="!scope.row.translators">None</span>
                        {{scope.row.translators}}
                    </template>
                </el-table-column>
                <el-table-column prop="status" label="Status">
                    <template slot-scope="scope">
                        <el-tag size="small" type="danger" v-if="'unfinished'===scope.row.status">{{scope.row.status}}
                        </el-tag>
                        <el-tag size="small" type="success" v-if="'finished'===scope.row.status">{{scope.row.status}}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column label="Operation" width="420" align="right">
                    <template slot-scope="scope">
                        <el-button
                                v-if="$root.isAdmin==='YES' || $root.user.username === scope.row.translators"
                                :title="scope.row.status === 'unfinished' ? 'Finished':'UnFinished'"
                                size="mini"
                                plain
                                type="primary"
                                :icon="scope.row.status === 'unfinished'?'el-icon-open':'el-icon-turn-off'"
                                @click="setStatus(scope.row.status,scope.row.code)">
                            {{scope.row.status === 'unfinished' ? 'Finished':'UnFinished'}}
                        </el-button>
                        <el-button
                                v-if="$root.isAdmin==='YES' || $root.user.username === scope.row.translators"
                                size="mini"
                                plain
                                type="warning"
                                icon="el-icon-s-order"
                                title="Translate"
                                @click="$router.push({name:'Translation',params:{id:$route.params.id,code:scope.row.code}})">
                            Translate
                        </el-button>
                        <!--v-if="$root.isAdmin==='YES' || $root.user.username === scope.row.translators"-->
                        <el-button
                                title="Appoint"
                                size="mini"
                                plain
                                type="info"
                                icon="el-icon-thumb"
                                @click="appointHandle(scope.row)">Appoint
                        </el-button>
                        <el-button
                                title="Download"
                                size="mini"
                                plain
                                :disabled="scope.row.status === 'unfinished'"
                                type="success"
                                icon="el-icon-download"
                                @click="showDown($route.params.id,scope.row.code)">
                        </el-button>
                    </template>

                </el-table-column>
            </el-table>
        </el-card>

        <el-dialog title="指派译者" :visible.sync="dialogVisible" width="30%">

            <el-form :model="form" label-width="100px">
                <el-form-item label="指派人员">
                    <el-autocomplete
                            clearable
                            v-model="selectedUser"
                            value-key="username"
                            :fetch-suggestions="querySearchAsync"
                            placeholder="请输入内容"
                            @select="handleSelect"
                            style="width: 100%"
                    />
                </el-form-item>

                <el-form-item label="翻译人员">
                    <el-tag
                            style="margin-right: 5px"
                            closable
                            v-for="(user,i) in form.users"
                            :key="user.user_id"
                            type="success" @close="closeTagHandle(i)">{{user.username}}
                    </el-tag>
                </el-form-item>
                <el-form-item>
                    <span style="color: #a0a3a8;font-size: 13px">找不到用户？麻烦对方登录一次 TMS 系统</span>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">取 消</el-button>
                <el-button type="primary" @click="AddTranslatorsHandle">确 定</el-button>
            </div>
        </el-dialog>

        <!--        下载-->
        <el-dialog title="下载" :visible.sync="downVisible" width="30%">
            <el-radio-group v-model="downParams.type">
                <el-radio label="json">JavaScript (json)</el-radio>
                <el-radio label="xml">Android (XML)</el-radio>
                <el-radio label="strings">iOS (strings)</el-radio>
                <el-radio label="php">PHP's file</el-radio>
                <el-radio label="csv">CSV</el-radio>
                <el-radio label="xlxs">XLXS</el-radio>
            </el-radio-group>
            <div slot="footer" class="dialog-footer">
                <el-button @click="downVisible = false">取 消</el-button>
                <el-button type="primary" @click="download">确 定</el-button>
            </div>
        </el-dialog>
        <el-dialog
                title="Add"
                :visible.sync="addVisible"
                width="30%">
            <el-form ref="form" :model="form" label-width="80px">
                <el-form-item label="Language">
                    <el-select v-model="addForm.language_code" style="width: 100%">
                        <el-option
                                v-for="lang of canSelectedLanguages"
                                :key="lang.language_id"
                                :value="lang.code"
                                :disabled="lang.disabled"
                                :label="lang.language"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="addVisible = false">取 消</el-button>
                <el-button type="primary" @click="addLanguageHandle">确 定</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>

    import axios from 'axios'
    import {Message} from 'element-ui'
    export default {
        name: "Language",
        data() {
            return {
                addForm: {
                    language_code: '',
                    project_id: parseInt(this.$route.params.id) || 0,
                },
                tableData: [],
                users: [],
                dialogVisible: false,
                addVisible: false,
                downVisible: false,
                selectedUser: '',
                form: {
                    users: [],
                    project_id: "",
                    project_language_id: "",
                    language_code: "",
                },
                downParams: {
                    projectId: '',
                    code: '',
                    type: 'json'
                },
                languages: [],
            }
        },
        created() {
            this.fetchProjectLanguage();
            this.fetchUsers();
            this.getLanguage();
        },
        computed: {
            canSelectedLanguages() {
                let lang = [];
                for (let item of this.languages) {
                    item.disabled = false;
                    for (let data of this.tableData) {
                        if (item.code !== data.code) {
                            continue
                        }
                        item.disabled = true;
                    }

                    lang.push(item)
                }
                return lang
            }
        },
        methods: {
            addLanguageHandle() {
                if (this.addForm.language_code === '') {
                    Message.error("请选择语言");
                    return
                }
                axios.post(`/api/v1/projects/${this.$route.params.id}/languages`,this.addForm).then(() => {
                    this.addVisible = false;
                    this.fetchProjectLanguage();
                })
            },
            async getLanguage() {
                let {data} = await axios.get('/api/v1/languages');
                this.languages = data
            },
            showDown(projectId, code) {
                this.downVisible = true;
                this.downParams.projectId = projectId;
                this.downParams.code = code;
            },
            setStatus(status, code) {

                let s = status === 'unfinished' ? 'finished' : 'unfinished';

                axios.put(`/api/v1/project/${this.$route.params.id}/translations/${code}`, {status: s}).then(() => {
                    this.fetchProjectLanguage();
                })

            },
            download() {
                let code = this.downParams.code;
                axios.get(`/api/v1/project/${this.downParams.projectId}/translations/${code}/export`, {
                    params: {type: this.downParams.type}
                }).then(res => {
                    let blob = new Blob([JSON.stringify(res.data)], {
                        type: 'application/octet-stream'
                    });

                    let url = URL.createObjectURL(blob);
                    let a = document.createElement('a');
                    a.style.display = 'none';
                    a.href = url;
                    a.setAttribute('download', `${code}.json`);   // 设置下载的文件名
                    document.body.appendChild(a);
                    a.click();
                    document.body.removeChild(a);      //下载完成移除dom元素
                    URL.revokeObjectURL(url);   //释放掉blob对象
                    this.downVisible = false;
                })
            },
            AddTranslatorsHandle() {
                axios.post(`/api/v1/projects/${this.$route.params.id}/translators`, this.form).then(() => {
                    this.dialogVisible = false;
                    this.fetchProjectLanguage();
                })
            },
            closeTagHandle(index) {
                this.form.users.splice(index, 1)
            },
            querySearchAsync(queryString, cb) {
                setTimeout(() => {
                    this.loading = false;
                    axios.get('/api/v1/users', {
                        params: {name: queryString},
                    }).then(({data}) => {

                        cb(data.data.data)
                    });
                }, 200);
            },
            handleSelect(row) {
                this.form.users.push({
                    user_id: row.user_id,
                    username: row.username,
                })
            },
            addTranslators() {
                axios.post(`/api/v1/projects/${this.$route.params.id}/translators`, this.form).then(() => {
                    this.dialogVisible = false;
                    this.fetchProjectLanguage();
                })
            },
            appointHandle(row) {
                this.dialogVisible = true;
                this.form.project_id = row.project_id;
                this.form.language_code = row.code;
                this.form.project_language_id = row.project_language_id;
            },

            remoteMethod(query) {
                if (query !== '') {
                    this.loading = true;
                    setTimeout(() => {
                        this.loading = false;
                        this.fetchUsers({username: query})
                    }, 200);
                }
            },
            async fetchUsers(paramsData = {}) {
                let {data} = await axios.get('/api/v1/users', {
                    params: paramsData,
                });
                this.users = data.data.data;
            },
            async fetchProjectLanguage() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/languages`);
                this.tableData = data.data;
            }
        }
    }
</script>

