<template>
    <div class="changelog">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">{{$t('home')}}</el-breadcrumb-item>
            <el-breadcrumb-item> Changelog</el-breadcrumb-item>
        </el-breadcrumb>
        <br>
        <el-card shadow="never">
            <div v-html="changelog"></div>
        </el-card>
    </div>
</template>

<script>
    import MarkdownIt from 'markdown-it'

    export default {
        name: "Changelog",
        data() {
            return {
                changelog: require('../../../CHANGELOG.md')
            }
        },
        methods: {
            markdown() {
                let md = new MarkdownIt()
                return md.render(this.changelog)
            }
        }
    }
</script>

