                    <template v-slot="{ row }">
                        <div style="display: flex;align-items: center;">
                            <span class="avatar">
                                <img v-if="row.imageUrl" :src="row.imageUrl" alt="">
                                <img v-else-if="!!row.file_path" :src="row.host + row.file_path" alt="">
                                <img v-else :src="noPic" style="width: 70%;"/>
                            </span>
                            <div style="display: flex;align-items: center;justify-content: center;flex-direction: column;margin-left: 10px;">
                                <el-upload
