                                        :before-upload="beforeAvatarUpload">
                                    <el-button size="small" type="primary">点击上传</el-button>
                                </el-upload>
                                <el-input
                                        style="width: 80px"
                                        @paste.native="(event)=>pasteHandle(row,event)"
                                        placeholder="粘贴图片"></el-input>
                            </div>
                        </div>
                    </template>
                </vxe-table-column>
                <vxe-table-column
                        field="description"
                        :edit-render="{name: 'input', defaultValue: ''}"
                        title="Description">
                </vxe-table-column>
                <vxe-table-column
                        field="tags" title="Tags">
                    <template v-slot="{row,rowIndex}">
                        <el-select
                                v-model="row.tags"
                                multiple
                                filterable
                                allow-create
                                default-first-option
                                placeholder="请选择或者输入标签">
                            <el-option
                                    v-for="item in projectTags"
                                    :key="item.value"
                                    :label="item.label"
                                    :value="item.value">
                            </el-option>
                        </el-select>
                    </template>
                </vxe-table-column>
                <vxe-table-column
                        field="value"
                        :title="`Value(default:${firstLanguage.name})`"
                        :edit-render="{name: 'input', defaultValue: ''}">
                </vxe-table-column>
                <vxe-table-column
                        title="操作" width="100">
                    <template v-slot="{ row, rowIndex }">
                        <vxe-button size="mini" status="danger"
                                    @click="deleteHandle(row.project_id,row.project_language_id,row.string_id)">删除
                        </vxe-button>
                    </template>
                </vxe-table-column>
            </vxe-table>
            <br>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    :page-size="30"
                    :current-page="currentPage"
                    @current-change="currentChangeHandle"
                    :total="count">
            </el-pagination>
        </el-card>

        <!--        <el-dialog-->
        <!--                title="提示"-->
        <!--                :show-close="false"-->
        <!--                :close-on-click-modal="false"-->
        <!--                :close-on-press-escape="false"-->
        <!--                :visible.sync="uploading"-->
        <!--                width="30%">-->
