            <!--                            <td>{{i}}</td>-->
            <!--                            <td>Key:{{o.key}}</td>-->
            <!--                            <td>Value: {{o.value}}</td>-->
            <!--                        </tr>-->
            <!--                    </table>-->
            <!--                </div>-->
            <!--            </div>-->
        </el-drawer>

        <el-dialog
                title="创建翻译条目"
                :visible.sync="addFormVisible"
                width="40%">
            <el-form :model="addForm" :rules="addRules" ref="addForm" label-width="100px" class="demo-addForm">
                <el-form-item label="默认添加语言" prop="region">
                    <span>简体中文</span>
                    <input type="hidden" v-model="addForm.language_code" value="zh-CN">
                </el-form-item>
                <el-form-item label="KEY" prop="name">
                    <el-input v-model="addForm.key"></el-input>
                </el-form-item>
                <el-form-item label="VALUE" prop="name">
