                </el-form-item>

                <el-form-item label="条目描述" prop="desc">
                    <!--                    <el-input type="textarea" v-model="addForm.description"></el-input>-->
                </el-form-item>

                <el-form-item label="标签" prop="resource">
                    <el-select
                            v-model="addForm.tags"
