                            placeholder="请选择或者输入标签">
                        <el-option
                                v-for="item in projectTags"
                                :key="item.value"
                                :label="item.label"
                                :value="item.value">
                        </el-option>
                    </el-select>
                </el-form-item>

            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button plain type="primary" @click="dialogVisible = false">完成并创建下一个</el-button>
                <el-button type="primary" @click="dialogVisible = false">完成</el-button>
