</template>

<script>
    import axios from 'axios'
    import {Message, MessageBox} from 'element-ui';
    import VueCookies from 'vue-cookies'

    export default {
        name: "Content",
        components: {

        },
        data() {
            return {
                addFormVisible: false,
                addForm: {
                    tags: [
                        // {tag_id:0,name:''}
                    ],
                    project_id: 0,
                    project_language_id: 0,
                    language_code: 0,
                    string_id: 0,
                    key: "",
                    value: "",
                    description: "",
                    file_path: "",

                },
                addRules: {},
                tableData: [],
                headers: {
                    "Authorization": ""
                },
                actionUrl: "",
                error: "",
                currentPage: 1,
                count: 0,
                screenshot: {
                    imageUrl: '',
                    action: `${process.env.VUE_APP_API_URL}/api/v1/upload`,
                },
                projectLanguages: [
                    {
                        language: "简体中文",
                        code: "zh-CN"
                    }
                ],
                drawer: false,
                outputData: [],
                uploadSuccess: false,
                searchVal: "",
                projectTags: [],
            }
        },
        computed: {
            firstLanguage() {
                let first = 'zh-CN';
                let firstName = '简体中文';
                let hasDefault = false;

                for (let item of this.projectLanguages) {
                    if (item['code'] === first) {
                        hasDefault = true
                    }
                }
                if (!hasDefault) {
                    first = this.projectLanguages[0]['code'] || first;
                    firstName = this.projectLanguages[0]['language'] || firstName;
                }
                return {
                    code: first,
                    name: firstName,
                };
            },
            output() {
                return Array.from(new Set(this.outputData));
            },
            noPic() {
                return require("../../assets/no_pic.png")
            }
        },
        created() {
            this.headers.Authorization = VueCookies.get("token");
            this.actionUrl = `${process.env.VUE_APP_API_URL}/api/v1/projects/${this.$route.params.id}/upload`;
            this.getContent({page: this.currentPage});
            this.fetchProjectLanguage();
            this.currentPage = this.$route.query.page || 1;

            //this.fetchStream()
        },

        beforeDestroy() {
            this.editor.destroy()
        },
        mounted() {
            this.getProjectTags();
        },
        methods: {
            getProjectTags() {

            },
            pasteHandle(row, e) {
                if (!(e.clipboardData && e.clipboardData.items)) {
                    return false;
                }
                for (let i = 0, len = e.clipboardData.items.length; i < len; i++) {
                    let item = e.clipboardData.items[i];
                    //console.log(item)
                    if (item.kind === "file") {
                        let blob = item.getAsFile();
                        //row.imageUrl = URL.createObjectURL(blob)

                        //console.log(row)
                        //row.file_path = 0
                        var params = new FormData();
                        params.append('file', blob, 'image.png')
                        axios.post(`${process.env.VUE_APP_API_URL}/api/v1/upload`, params, {
                            headers: {
                                "Content-Type": "multipart/form-data"
                            }
                        }).then(({data}) => {
                            this.$set(row, 'imageUrl', URL.createObjectURL(blob))
                            row.imageUrl = data.data.host + data.data.filename;
                            row.file_path = data.data.filename;
                            this.editClosedEvent({row})
                        })
                    }
                }

            },

            saveKeyContent(row, params = {}) {
                axios.post(`/api/v1/projects/${this.$route.params.id}/keys/contents`, params).then(({data}) => {
                    this.$set(row, 'string_id', data.data.string_id)
                })
            },
            updateKeyContent(string_id, params) {
                axios.put(`/api/v1/projects/${this.$route.params.id}/keys/${string_id}/contents`, params).then((data) => {

                })
            },

            deleteKey(string_id) {
                axios.delete(`/api/v1/projects/${this.$route.params.id}/keys/${string_id}/contents`).then(() => {
                    this.getContent({'search': this.searchVal})
                })
            },

            editClosedEvent({row}) {
                let params = {
                    project_id: parseInt(this.$route.params.id),
                    project_language_id: row.project_language_id || null,
                    language_code: row.language_code || 'zh-CN',
                    string_id: row.string_id || null,
                    key: row.key,
                    value: row.value,
                    description: row.description,
                    file_path: row.file_path,
                }
                if (params.string_id == null) {
                    if (row.key === "") return;
                    this.saveKeyContent(row, params);
                    return
                }
                this.updateKeyContent(row.string_id, params)
            },

            searchHandle() {
                this.getContent({'search': this.searchVal})
            },
            fetchStream(query = null) {
                let url = `${process.env.VUE_APP_API_URL}/api/v1/projects/${this.$route.params.id}/stream`;
                let source = new EventSource(url);
                source.addEventListener('message', event => {
                    let resp = JSON.parse(event.data);
                    // handle message

                    let data = resp.data;

                    if (resp.error && resp.error !== "") {
                        source.close()
                    }

                    this.outputData.push(JSON.parse(data));

                    //source.close()
                    //console.log(data);
                    if (data === 'eof') {
                        //this.$apollo.queries.task.refetch();
                        source.close();
                    }

                }, false);
                //获取错误时，关闭流
                source.addEventListener('error', event => {
                    source.close();

                })
            },

            handleAvatarSuccess(response, file, fileList, row) {
                //console.log(response, file, fileList,row)
                row.imageUrl = response.data.host + response.data.filename;
                row.file_path = response.data.filename;
                //this.screenshot.imageUrl = res.data.host + res.data.filename
                this.editClosedEvent({row})
            },
            beforeExcelUpload(file) {
                console.log(file.type);
                const isExcel = file.type === 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet' || 'text/csv';

                if (!isExcel) {
                    Message.error('上传文件只能是 Excel 或者 Csv 格式!');
                }

                return isExcel
            },
            beforeAvatarUpload(file) {
                const isJPG = file.type === 'image/jpeg' || file.type === 'image/png';
                const isLt2M = file.size / 1024 / 1024 < 3;

                if (!isJPG) {
                    Message.error('上传头像图片只能是 JPG/PNG 格式!');
                }
                if (!isLt2M) {
                    Message.error('上传头像图片大小不能超过 3MB!');
                }
                return isJPG && isLt2M;
            },

            currentChangeHandle(page) {
                this.currentPage = page;
                this.$router.push({query: {page: page}});
                this.getContent({page: page})
            },
            onSuccess(response, file, fileList) {
                //this.uploading = false;
                //setTimeout(()=>this.fetchStream(),1000)
                this.uploadSuccess = true
            },

            deleteHandle(project_id, project_language_id, string_id) {
                if (typeof string_id === 'undefined') {
                    this.getContent({page: this.currentPage});
                    return
                }
                MessageBox.confirm('删除此 KEY 会把所有的翻译的内容删除, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    this.deleteKey(string_id)
                }).catch(() => {

                });
            },
            // nameChangeEvent({row, rowIndex, column, columnIndex}, event) {
            //     let val = event.target.value;
            //     if (row.identifier_id) return;
            //     setTimeout(() => {
            //
            //         axios.get(`/api/v1/projects/${this.$route.params.id}/keys/search`, {params: {s: val}})
            //             .then(() => {
            //             })
            //             .catch(error => {
            //                 Message.error(error.response.data.error);
            //             })
            //     }, 300)
            // },
            async getContent(params = {}) {

                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/translations/contents`, {params: params});
                this.tableData = data.data.data;
                this.count = data.data.paging.count
            },
            async insertEvent(row) {
                let record = {
                    key: '',
                    value: "",
                    project_id: this.$route.params.id
                };
                let {row: newRow} = await this.$refs.xTable.insertAt(record, row);
                await this.$refs.xTable.setActiveCell(newRow, 'key')
            },
            saveEvent() {
                const {insertRecords, removeRecords, updateRecords} = this.$refs.xTable.getRecordset();
                let data = {
                    project_id: Number(this.$route.params.id),
                    language_code: this.firstLanguage.code || 'zh-CN',
                    insert: insertRecords,
                    remove: removeRecords,
                    update: updateRecords,
                };
                axios.put(`/api/v1/projects/${this.$route.params.id}/batch-translations/contents`, data)
                    .then(() => {
                        this.getContent({page: this.currentPage});
                        Message.success("保存成功");
                    })
            },
            async fetchProjectLanguage() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/languages`);
                this.projectLanguages = data.data;
            }
        }
    }
</script>

