<template>
    <div class="job">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">{{$t('home')}}</el-breadcrumb-item>
            <el-breadcrumb-item>{{$t('task_center')}}</el-breadcrumb-item>
        </el-breadcrumb>
        <br>
        <el-card shadow="never">
            <el-table
                    :data="jobs"
                    style="width: 100%">
                <el-table-column
                        prop="project_name"
                        :label="$t('project_name')">
                </el-table-column>
                <el-table-column
                        prop="description"
                        :label="$t('description')">
                </el-table-column>
                <el-table-column
                        prop="language"
                        :label="$t('target_language')">
                </el-table-column>
                <el-table-column
                        prop="status"
                        :label="$t('status')">
                    <template slot-scope="scope">
                        <el-tag size="small" type="danger" v-if="'unfinished'===scope.row.status">{{scope.row.status}}
                        </el-tag>
                        <el-tag size="small" type="success" v-if="'finished'===scope.row.status">{{scope.row.status}}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column align="right" :label="$t('operation')">
                    <template slot-scope="scope">

                        <el-button
                                plain
                                size="mini"
                                type="warning"
                                icon="el-icon-s-order"
                                @click="$router.push({name:'Translation',params:{id:scope.row.project_id,code:scope.row.language_code}})">
                            Translate
                        </el-button>
                        <el-button
                                type="primary"
                                size="mini"
                                plain
                                icon="el-icon-stopwatch"
                                :loading="scanLoadingIndex === (scope.row.project_id + scope.row.language_code)"
                                @click="scanLanguageStatus(scope.row.project_id,scope.row.language_code)"
                        >Scan
                        </el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>
    </div>
</template>

<script>
    import axios from 'axios'
    import {Message} from "element-ui";

    export default {
        name: "TaskCenter",
        data() {
            return {
                jobs: [],
                scanLoadingIndex: 0,
                checkDialogVisible: false,
                checkData: [],
            }
        },
        created() {
            this.fetchJobs();
        },
        methods: {
            async scanLanguageStatus(projectId, code) {
                this.scanLoadingIndex = projectId + code

                await axios.put(`/api/v1/projects/${projectId}/languages/${code}/scan`).then(({data}) => {
                    let status = data.data.status || false
                    let count = data.data.uf_keys ? data.data.uf_keys.length : 0
                    this.fetchJobs();
                    this.scanLoadingIndex = 0
                    Message({
                        message: status ? "完成翻译" : `有${count}条翻译未完成`,
                        type: status ? "success" : "error"
                    })
                })
            },
            setStatus(projectId, code, status) {
                let s = status === 'unfinished' ? 'finished' : 'unfinished';

                axios.put(`/api/v1/project/${projectId}/translations/${code}`, {status: s}).then(() => {
                    this.fetchJobs();
                })
            },
            async fetchJobs() {
                let {data} = await axios.get("/api/v1/my/jobs");
                this.jobs = data.data;
            }
        }
    }
</script>

