        </el-breadcrumb>
        <br>
        <el-card shadow="never">
            <el-row :gutter="15" type="flex" justify="space-between">
                <el-col :span="16" style="flex: 1">
                    <el-button type="primary" @click="$router.push({name:'AddProject'})">{{$t('create_project')}}</el-button>
                </el-col>
                <el-col :span="6">
                    <el-input placeholder="Search..." size="small" v-model="searchValue" class="input-with-select" clearable>
                        <el-button slot="append" icon="el-icon-search" @click="fetchProject({name:searchValue})"/>
                    </el-input>
                </el-col>
            </el-row>

            <el-table :data="tableData" style="width: 100%">
                <el-table-column prop="name" :label="$t('project_name')" width="180">
                </el-table-column>
                <el-table-column prop="description" :label="$t('description')" min-width="200">
                </el-table-column>
                <el-table-column prop="founder" :label="$t('founder')">
                </el-table-column>
                <el-table-column prop="permission" :label="$t('permission')">
                </el-table-column>
                <el-table-column prop="created_at" :label="$t('create_time')" width="190">
                </el-table-column>
                <el-table-column prop="updated_at" :label="$t('update_time')" width="190">
                </el-table-column>
                <el-table-column :label="$t('operation')" width="360" align="right">
                    <template slot-scope="scope">
                        <el-button
                                size="mini"
                                plain
                                type="primary"
                                icon="el-icon-view"
                                @click="$router.push({name:'ProjectOverview',params:{id:scope.row.project_id}})">{{$t('view')}}
                        </el-button>
<!--                        <el-button-->
<!--                                size="mini"-->
<!--                                plain-->
<!--                                type="primary"-->
<!--                                icon="el-icon-view"-->
<!--                                @click="$router.push({name:'Language',params:{id:scope.row.project_id}})">View1-->
<!--                        </el-button>-->
                        <el-button size="mini"
                                   v-if="$root.isAdmin === 'YES' || $root.user.username === scope.row.founder"
                                   plain type="info" icon="el-icon-receiving"
                                   @click="$router.push({name:'Item',params:{id:scope.row.project_id}})">{{$t('item')}}
                        </el-button>
<!--                        <el-button size="mini"-->
<!--                                   plain type="success" icon="el-icon-download">Download-->
<!--                        </el-button>-->
