<!--                        </el-button>-->
                    </template>

                </el-table-column>
            </el-table>
            <br>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    @current-change="currentChangeHandle"
                    :total="count">
            </el-pagination>
        </el-card>
    </div>

</template>

<script>
    import axios from 'axios'
    import {Message, MessageBox} from 'element-ui';
    import storejs from 'storejs';

    export default {
        name: "Project",
        data() {
            return {
                tableData: [],
                count: 0,
                searchValue:'',
            }
        },

        created() {
            this.fetchProject();
        },
        methods: {

            currentChangeHandle(page) {
                this.fetchProject({page: page})
            },
            deleteHandle(projectId) {
                MessageBox.confirm('此操作将永久删除该项目, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    axios.delete("/api/v1/projects/" + projectId).then(() => {
                        Message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    });
                }).catch(() => {

                });
            },
            async fetchProject(params = {}) {
                let {data} = await axios.get("/api/v1/projects", {params: params});
                this.tableData = data.data.data;
            }
        }
    }
</script>

