<template>
    <div class="assign">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">Home</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name:'ProjectOverview',params:{'id':$route.params.id} }">{{project.name ||
                'Project Management'}}
            </el-breadcrumb-item>
            <el-breadcrumb-item> 指派翻译</el-breadcrumb-item>
        </el-breadcrumb>
        <br>
        <el-card shadow="never" style="margin-bottom: 20px">
            <el-form label-width="120px">
                <el-form-item label="项目">
                    <span>{{project.name}}</span>
                </el-form-item>
                <el-form-item label="指派语言">
                    {{$route.query.code||""}}
                </el-form-item>
            </el-form>
        </el-card>

        <el-card shadow="never" style="margin-bottom: 20px">
            <el-form :inline="true" :model="form" class="demo-form-inline">
                <el-form-item label="添加翻译人员">
                    <el-autocomplete
                            clearable
                            v-model="selectedUser"
                            value-key="username"
                            :fetch-suggestions="querySearchAsync"
                            placeholder="请选择"
                            @select="handleSelect"
                            style="width: 100%"
                    />
                </el-form-item>
                <el-form-item label="发送通知?">
                    <el-switch
                            v-model="form.is_send_mail"
                            active-color="#13ce66"
                            inactive-color="#ff4949">
                    </el-switch>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">添加</el-button>
                </el-form-item>
            </el-form>
            <el-table style="width: 100%" :data="translators">
                <el-table-column label="ID" prop="project_translator_id"></el-table-column>
                <el-table-column label="翻译语言" prop="language_code"></el-table-column>
                <el-table-column label="翻译人员" prop="username"></el-table-column>
                <el-table-column label="操作" align="right">
                    <template slot-scope="scope">
                        <el-button type="danger" @click="delHandle(scope.row.project_translator_id)">删除</el-button>
                    </template>
                </el-table-column>
            </el-table>
        </el-card>

    </div>
</template>

<script>
    import axios from "axios";
    import Message from "element-ui/packages/message/src/main";
    import {MessageBox} from "element-ui";

    export default {
        name: "Assign",
        data() {
            return {
                selectedUser: "",
                form: {
                    users: [],
                    project_id: "",
                    project_language_id: "",
                    language_code: "",
                    is_send_mail: false,
                },
                project: {
                    name: '',
                    description: '',
                    founder: "",
                    founder_id: "",
                    updated_at: '',
                },
                translators: []
            }
        },
        created() {
            this.fetchProject()
            this.fetchTranslators();
            this.form.project_id = parseInt(this.$route.params.id) || 0
            this.form.project_language_id = parseInt(this.$route.params.lid) || 0
            this.form.language_code = this.$route.params.code
        },
        methods: {
            delHandle(projectTranslatorId) {
                MessageBox.confirm('此操作将永久删除该文件, 是否继续?', '提示', {
                    confirmButtonText: '确定',
                    cancelButtonText: '取消',
                    type: 'warning'
                }).then(() => {
                    axios.delete(`/api/v1/projects/${this.$route.params.id}/languages/${this.$route.params.code}/translators/${projectTranslatorId}`).then(() => {
                        this.fetchTranslators();
                        Message({
                            type: 'success',
                            message: '删除成功!'
                        });
                    })
                });
            },
            onSubmit() {
                if (this.form.users.length === 0) {
                    return
                }
                axios.post(`/api/v1/projects/${this.$route.params.id}/languages/${this.$route.params.code}/translators`, this.form).then(() => {
                    this.dialogVisible = false;
                    this.fetchTranslators();
                })
            },
            querySearchAsync(queryString, cb) {
                setTimeout(() => {
                    this.loading = false;
                    axios.get('/api/v1/users', {
                        params: {name: queryString},
                    }).then(({data}) => {

                        cb(data.data.data)
                    });
                }, 200);
            },
            async fetchTranslators() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/languages/${this.$route.params.code}/translators`);
                this.translators = data.data;
            },
            async fetchProject() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}`);
                this.project = data.data;
            },

            handleSelect(row) {
                this.form.users.push({
                    user_id: row.user_id,
                    username: row.username,
                    email: row.email,
                });
            }
        }
    }
</script>

<style lang="scss" scoped>
    .demo-form-inline {
        background-color: #f6f6f6;
        padding: 10px;
        border-radius: 4px;

        .el-form-item {
            margin-bottom: 0;
        }

        margin-bottom: 10px;
    }
</style>