                                <div class="card-content">
                                    <span class="name">{{user.nickname}}</span>
                                    <span class="user_type">[{{$t('administrator')}}]{{user.username}}</span>
                                </div>
                                <div class="card-menu">
                                    <el-dropdown @command="(command)=>commandHand(command,user.user_id)">
                                    <span class="el-dropdown-link">
    <svg t="1585576655270" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
         p-id="3440" width="20" height="20"><path
            d="M580.152 309.97H124.496C98.923 309.97 78 290.627 78 266.985S98.923 224 124.496 224h455.656c25.572 0 46.495 19.343 46.495 42.985-0.775 23.642-21.698 42.985-46.495 42.985zM899.33 309.97H733.496c-25.573 0-46.496-19.343-46.496-42.985S707.923 224 733.496 224H899.33c25.572 0 46.495 19.343 46.495 42.985-0.775 23.642-21.698 42.985-46.495 42.985zM898.646 554.97h-774.15C98.923 554.97 78 535.627 78 511.985S98.923 469 124.496 469h774.15c25.573 0 46.496 19.343 46.496 42.985-0.775 23.642-21.698 42.985-46.496 42.985zM898.646 799.97h-774.15C98.923 799.97 78 780.627 78 756.985S98.923 714 124.496 714h774.15c25.573 0 46.496 19.343 46.496 42.985-0.775 23.642-21.698 42.985-46.496 42.985z"
            p-id="3441" fill="#8a8a8a"></path></svg>
  </span>
                                        <el-dropdown-menu slot="dropdown">
                                            <el-dropdown-item
                                                    command="set_edit"
