                                                {{$t('set_nomal_member')}}
                                            </el-dropdown-item>
                                            <el-dropdown-item
                                                    command="set_view"
                                                    v-if="$root.isAdmin === 'YES' || $root.user.username === project.founder_id">
                                                {{$t('set_readonly_member')}}
                                            </el-dropdown-item>
                                            <el-dropdown-item
                                                    command="remove"
                                                    v-if="$root.isAdmin === 'YES' || $root.user.username === project.founder_id">
                                                {{$t('exit_project')}}
                                            </el-dropdown-item>
                                        </el-dropdown-menu>
                                    </el-dropdown>
                                </div>
                            </div>
                        </el-col>
                    </el-row>
                    <br>
                    <h3 class="card-title">{{$t('project_members')}}</h3>
                    <el-row v-if="members.length > 0">
                        <el-col :span="6" v-for="user in members" :key="user.user_id" v-if="user.permission !=='ALL'">
                            <div class="card">
                                <div class="card-avatar">
                                    {{user.nickname[0].toLocaleUpperCase()}}
                                </div>
                                <div class="card-content">
                                    <span class="name">{{user.nickname}}</span>
                                    <span class="user_type">[{{permType[user.permission]}}]{{user.username}}</span>
                                </div>
                                <div class="card-menu">
                                    <el-dropdown @command="(command)=>commandHand(command,user.user_id)">
                                    <span class="el-dropdown-link">
    <svg t="1585576655270" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg"
         p-id="3440" width="20" height="20"><path
