            p-id="3441" fill="#8a8a8a"></path></svg>
  </span>
                                        <el-dropdown-menu slot="dropdown">
                                            <el-dropdown-item
                                                    command="set_all"
                                                    v-if="$root.isAdmin === 'YES' || $root.user.username === project.founder_id">
                                                {{$t('set_administrator')}}
                                            </el-dropdown-item>
                                            <el-dropdown-item
                                                    command="set_edit"
                                                    v-if="user.permission==='VIEW' && ($root.isAdmin === 'YES' || $root.user.username === project.founder_id)">
                                                {{$t('set_nomal_member')}}
                                            </el-dropdown-item>
                                            <el-dropdown-item
                                                    command="set_view"
                                                    v-if="user.permission === 'EDIT' && ($root.isAdmin === 'YES' || $root.user.username === project.founder_id)">
                                                {{$t('set_readonly_member')}}
                                            </el-dropdown-item>
                                            <el-dropdown-item command="remove">{{$t('kick_the_project')}}
                                            </el-dropdown-item>
                                        </el-dropdown-menu>
                                    </el-dropdown>
                                </div>
                            </div>
                        </el-col>
                    </el-row>
                    <span style="color: #777777">{{$t('no_members')}}</span>
                </el-card>
                <el-card shadow="never">
                    <el-button type="primary" @click="addVisible = true">Add</el-button>
                    <el-table :data="tableData" style="width: 100%">
                        <el-table-column prop="language" label="Language Name">
                        </el-table-column>
                        <el-table-column prop="code" label="Language Code">
                        </el-table-column>
                        <el-table-column prop="translators" label="Translator">
                            <template slot-scope="scope">
                                <span v-if="!scope.row.translators">None</span>
                                {{scope.row.translators}}
                            </template>
                        </el-table-column>
                        <el-table-column prop="status" label="Status" width="150">
                            <template slot-scope="scope">
                                <el-tag
                                        size="mini"
                                        plain
                                        :icon="scope.row.status === 'unfinished'?'el-icon-open':'el-icon-turn-off'"
                                        :type="scope.row.status === 'unfinished'?'danger':'success'">
                                    {{scope.row.status !== 'unfinished' ? 'Finished':'Unfinished'}}
                                </el-tag>
                            </template>
                        </el-table-column>
                        <el-table-column :label="$t('operation')" width="350" align="right">
                            <template slot-scope="scope">
                                <el-button
                                        size="mini"
                                        plain
                                        type="warning"
                                        icon="el-icon-s-order"
                                        title="Translate"
                                        v-if="$root.isAdmin === 'YES' || isProjectEdit || $root.user.username === project.founder"
                                        @click="$router.push({name:'Translation',params:{id:$route.params.id,code:scope.row.code}})">
                                    {{$t('translation')}}
                                </el-button>

                                <el-button
                                        size="mini"
                                        plain
                                        type="info"
                                        icon="el-icon-thumb"
                                        v-if="$root.isAdmin === 'YES' || isProjectEdit || $root.user.username === project.founder"
                                        @click="$router.push({
                                        name:'Assign',
                                        params:{
                                            id:$route.params.id,
                                            lid:scope.row.project_language_id,
                                            code:scope.row.code,
                                            },
                                            query:{
                                            code:scope.row.language||''
                                        }})">
                                    {{$t('assign')}}
                                </el-button>
                                <el-button
                                        type="primary"
                                        size="mini"
                                        plain
                                        icon="el-icon-stopwatch"
                                        :loading="scanLoadingIndex === scope.row.code"
                                        @click="scanLanguageStatus($route.params.id,scope.row.code)"
                                >{{$t('scan')}}
                                </el-button>
                            </template>

                        </el-table-column>
                    </el-table>

                </el-card>

            </el-col>
            <el-col :span="8">
                <el-timeline v-if="logs.length > 0">
                    <el-timeline-item :timestamp="log.op_time" placement="top" v-for="log of logs" :key="log.log_id">
                        <el-card shadow="never">
                            <h4>{{log.description}}</h4>
                            <p>{{log.username}} {{$t('submitted_on')}} {{log.op_time}}</p>
                        </el-card>
                    </el-timeline-item>
                </el-timeline>
                <el-card shadow="never" v-if="logs.length === 0">{{$t('no_operation')}}</el-card>
            </el-col>
        </el-row>
        <el-dialog
                title="Add"
                :visible.sync="addVisible"
                width="30%">
            <el-form ref="addForm" :model="addForm" label-width="80px">
                <el-form-item label="Language">
                    <el-select v-model="addForm.language_code" style="width: 100%">
                        <el-option
                                v-for="lang of canSelectedLanguages"
                                :key="lang.language_id"
                                :value="lang.code"
                                :disabled="lang.disabled"
                                :label="lang.language"></el-option>
                    </el-select>
                </el-form-item>
            </el-form>

            <div slot="footer" class="dialog-footer">
                <el-button @click="addVisible = false">{{$t('cancel')}}</el-button>
                <el-button type="primary" @click="addLanguageHandle">{{$t('ok')}}</el-button>
            </div>
        </el-dialog>

        <el-dialog title="$t('assign_translator')" :visible.sync="dialogVisible" width="30%">

            <el-form :model="form" label-width="100px">
                <el-form-item :label="$t('please_select')">
                    <el-autocomplete
                            clearable
                            v-model="selectedUser"
                            value-key="username"
                            :fetch-suggestions="querySearchAsync"
                            placeholder="Search..."
                            @select="handleSelect"
                            style="width: 100%"
                    />
                </el-form-item>

                <el-form-item :label="$t('translator')">
                    <el-tag
                            style="margin-right: 5px"
                            closable
                            v-for="(user,i) in form.users"
                            :key="user.user_id"
                            type="success" @close="closeTagHandle(i)">{{user.username}}
                    </el-tag>
                </el-form-item>
                <el-form-item>
                    <el-checkbox v-model="form.is_send_mail">{{$t('to_send_notification')}}</el-checkbox>
                </el-form-item>
                <el-form-item>
                    <span style="color: #a0a3a8;font-size: 13px">{{$t('cannot_find_the_user')}}</span>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="dialogVisible = false">{{$t('cancel')}}</el-button>
                <el-button type="primary" @click="AddTranslatorsHandle">{{$t('ok')}}</el-button>
            </div>
        </el-dialog>
        <el-dialog
                :title="$t('result')"
                :visible.sync="checkDialogVisible"
                width="40%">
            <el-table
                    :data="checkData"
                    stripe
                    border
                    v-if="this.checkData.length > 0"
                    style="width: 100%">
                <el-table-column prop="language" :label="$t('language')">
                    <template slot-scope="scope">
                        <!--                        #67c23a-->
                        <span :style="{ color:scope.row.status ? '#67c23a':'#555' }">{{scope.row.language}}</span>
                    </template>
                </el-table-column>
                <el-table-column prop="status" :label="$t('status')">
                    <template slot-scope="scope">
                        <el-tag
                                :effect="scope.row.status ? 'dark':'plain'"
                                :type="scope.row.status?'success':'info'">
                            {{scope.row.status?'Finished':'Unfinished'}}
                        </el-tag>
                    </template>
                </el-table-column>
                <el-table-column :label="$t('untranslated')">
                    <template slot-scope="scope">
                        <span :style="{ color:scope.row.status ? '#67c23a':'#555' }">
                            {{scope.row.uf_keys?scope.row.uf_keys.length : 0}}
                        </span>
                    </template>
                </el-table-column>
            </el-table>
            <div v-if="this.checkData.length === 0">
                <i class="el-icon-loading"></i>
                <span>{{$t('loading')}}...</span>
            </div>
        </el-dialog>
        <el-dialog
                title="Edit"
                :visible.sync="editDialogVisible"
                width="30%">
            <el-form ref="form" :model="editForm" label-width="80px">
                <el-form-item :label="$t('project_name')">
                    <el-input v-model="editForm.name"></el-input>
                </el-form-item>
                <el-form-item :label="$t('description')">
                    <el-input
                            :autosize="{ minRows: 2, maxRows: 4}"
                            type="textarea"
                            v-model="editForm.description"></el-input>
                </el-form-item>
            </el-form>
            <div slot="footer" class="dialog-footer">
                <el-button @click="editDialogVisible = false">{{$t('cancel')}}</el-button>
                <el-button type="primary" @click="editHandle">{{$t('ok')}}</el-button>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import axios from "axios";
    import {Message, MessageBox} from "element-ui";

    export default {
        name: "Overview",
        data() {
            return {
                permType: {
                    "ALL": this.$t('administrator'),
                    "EDIT": this.$t('rw'),
                    "VIEW": this.$t('readonly'),
                },
                activeName: "first",
                tableData: [],
                addVisible: false,
                languages: [],
                members: [],
                project: {
                    name: '',
                    description: '',
                    founder: "",
                    founder_id: "",
                    updated_at: '',
                },
                addForm: {
                    language_code: '',
                    project_id: parseInt(this.$route.params.id) || 0,
                },
                dialogVisible: false,
                form: {
                    users: [],
                    project_id: "",
                    project_language_id: "",
                    language_code: "",
                    is_send_mail: false,
                },
                downParams: {
                    projectId: '',
                    code: '',
                    type: 'json'
                },
                selectedUser: '',
                selectedMember: '',
                logs: [],
                scanLoadingIndex: 0,
                checkDialogVisible: false,
                checkData: [],
                editForm: {
                    name: '',
                    description: ''
                },
                editDialogVisible: false,
            }
        },
        computed: {
            isProjectEdit() {
                let is = false
                for (let member of this.members) {
                    if (member.user_id !== this.$root.user.id) {
                        continue
                    }
                    if (member.is_admin === "YES") {

                        is = true
                        break
                    }

                    if (this.project.founder_id === this.$root.user.user_id) {

                        is = true
                        break
                    }

                    if (member.permission === "EDIT") {

                        is = true
                        break
                    }
                }
                return is
            },
            isProjectAdmin() {
                let is = false
                //是否是项目的管理员
                for (let member of this.members) {
                    if (member.user_id !== this.$root.user.id) {
                        continue
                    }
                    if (member.is_admin === "YES") {

                        is = true
                        break
                    }

                    if (this.project.founder_id === this.$root.user.user_id) {

                        is = true
                        break
                    }

                    if (member.permission === "ALL") {

                        is = true
                        break
                    }
                }
                return is;
            },
            canSelectedLanguages() {
                let lang = [];
                for (let item of this.languages) {
                    item.disabled = false;
                    for (let data of this.tableData) {
                        if (item.code !== data.code) {
                            continue
                        }
                        item.disabled = true;
                    }

                    lang.push(item)
                }
                return lang
            }
        },
        created() {
            this.fetchProjectLanguage();
            this.fetchProject();
            this.getLanguage();
            this.fetchProjectMember();
            this.fetchProjectLogs();
        },
        methods: {
            editHandle() {
                this.editDialogVisible = true
                axios.put(`/api/v1/projects/${this.$route.params.id}`, this.editForm).then(() => {
                    this.fetchProject();
                    this.editDialogVisible = false
                })
            },
            async scanLanguageStatus(projectId, code) {
                this.scanLoadingIndex = code

                await axios.put(`/api/v1/projects/${projectId}/languages/${code}/scan`).then(({data}) => {
                    let status = data.data.status || false
                    let count = data.data.uf_keys ? data.data.uf_keys.length : 0
                    this.fetchProjectLanguage();
                    this.scanLoadingIndex = 0
                    Message({
                        message: status ? "完成翻译" : `有${count}条翻译未完成`,
                        type: status ? "success" : "error"
                    })
                })
            },
            commandMenu(command) {
                if (command === 'delete') {
                    this.deleteHandle(this.$route.params.id)
                }
                if (command === 'checkStatus') {
                    this.checkProjectStatus()
                }
                if (command === 'edit') {
                    this.editDialogVisible = true
                    this.editForm.name = this.project.name;
                    this.editForm.description = this.project.description;
                }
            },
            checkProjectStatus() {
                this.checkDialogVisible = true
                this.checkData = [];
                for (let item of this.tableData) {
                    axios.put(`/api/v1/projects/${this.$route.params.id}/languages/${item.project_language_id}/scan`)
                        .then(({data}) => {
                            this.checkData.push({...data.data, ...{language: item.language}})

                        })
                }
            },
            deleteHandle(projectId) {
                const h = this.$createElement;
                MessageBox.prompt('', '⚠️', {
                    message: h('span', {style: 'color: #f56c6c'}, this.$t('delete_project_desc_waring') + ': '),
                    confirmButtonText: this.$t('ok'),
                    cancelButtonText: this.$t('cancel'),
                    inputValidator: (val) => {
                        return val === this.project.name;
                    },
                    inputPlaceholder: this.$t('enter_project_name'),
                }).then(({value}) => {
                    axios.delete("/api/v1/projects/" + projectId).then(() => {
                        Message({
                            type: 'success',
                            message: this.$t('successfully_deleted')
                        });
                        this.$router.replace({name: "Project"})
                    });
                }).catch(() => {

                });
            },
            async fetchProjectLogs() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/logs`);
                this.logs = data.data;
            },

            commandHand(command, userId) {
                if (command === 'remove') {
                    this.fetchDeleteMember(userId)
                    return
                }
                let perms = {
                    "set_all": "ALL",
                    "set_edit": "EDIT",
                    "set_view": "VIEW",
                };
                this.fetchUpdateMember(userId, perms[command])
            },
            handleSelectMember(row) {
                axios.post(`/api/v1/projects/${this.$route.params.id}/members`, {
                    user_id: row.user_id,
                    permission: "EDIT"
                }).then(() => {
                    this.fetchProjectMember();
                })
            },
            handleSelect(row) {
                this.form.users.push({
                    user_id: row.user_id,
                    username: row.username,
                    email: row.email,
                });
            },
            async fetchUpdateMember(userId, perm = "EDIT") {
                await axios.put(`/api/v1/projects/${this.$route.params.id}/members/${userId}`, {
                    user_id: userId,
                    permission: perm
                });
                await this.fetchProjectMember();
            },
            async fetchDeleteMember(userId) {
                await axios.delete(`/api/v1/projects/${this.$route.params.id}/members/${userId}`);
                await this.fetchProjectMember();
            },
            querySearchAsync(queryString, cb) {
                setTimeout(() => {
                    this.loading = false;
                    axios.get('/api/v1/users', {
                        params: {name: queryString},
                    }).then(({data}) => {

                        cb(data.data.data)
                    });
                }, 200);
            },
            AddTranslatorsHandle(code) {
                axios.post(`/api/v1/projects/${this.$route.params.id}/languages/${code}/translators`, this.form).then(() => {
                    this.dialogVisible = false;
                    this.fetchProjectLanguage();
                })
            },
            closeTagHandle(index) {
                this.form.users.splice(index, 1)
            },
            appointHandle(row) {
                this.dialogVisible = true;
                this.form.project_id = row.project_id;
                this.form.language_code = row.code;
                this.form.project_language_id = row.project_language_id;
                this.fetchProjectTranslations(row.code)
            },
            async fetchProjectTranslations() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/languages/${this.form.language_code}/translators`);
                this.form.users = data.data;

            },
            setStatus(status, code) {

                let s = status === 'unfinished' ? 'finished' : 'unfinished';

                axios.put(`/api/v1/project/${this.$route.params.id}/translations/${code}`, {status: s}).then(() => {
                    this.fetchProjectLanguage();
                })

            },
            async fetchProjectMember() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/members`);
                this.members = data.data;
            },
            async getLanguage() {
                let {data} = await axios.get('/api/v1/languages');
                this.languages = data
            },
            addLanguageHandle() {
                if (this.addForm.language_code === '') {
                    Message.error(this.$t('please_select_language'));
                    return
                }
                axios.post(`/api/v1/projects/${this.$route.params.id}/languages`, this.addForm).then(() => {
                    this.addVisible = false;
                    this.fetchProjectLanguage();
                })
            },
            async fetchProject() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}`);
                this.project = data.data;
            },
            async fetchProjectLanguage() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/languages`);
                this.tableData = data.data;
            }
        }
    }
</script>

