                            <li class="color-circle" v-for="(c,i) of circle" :key="c">
                                <a class="color-circle-btn" :style="{backgroundColor:`${c}`}" @click="checkCircle(i)">
                                    <svg t="1588079425473" class="icon" viewBox="0 0 1024 1024" version="1.1"
                                         v-if="checkIndex === i"
