                                        <path d="M883.2 328.533333 883.2 328.533333l-426.666667 426.666667 0 0C450.133333 763.733333 439.466667 768 426.666667 768s-23.466667-4.266667-29.866667-12.8l0 0-256-256 0 0C132.266667 492.8 128 482.133333 128 469.333333c0-23.466667 19.2-42.666667 42.666667-42.666667 12.8 0 23.466667 4.266667 29.866667 12.8l0 0L426.666667 665.6l396.8-396.8 0 0c8.533333-8.533333 19.2-12.8 29.866667-12.8 23.466667 0 42.666667 19.2 42.666667 42.666667C896 311.466667 891.733333 322.133333 883.2 328.533333z"
                                              p-id="4149" fill="#ffffff"></path>
                                    </svg>
                                </a>
                            </li>
                        </ul>
                        <el-button type="primary" style="width: 100%" @click="createTag">创建</el-button>
                    </div>
