        </transition>
    </div>
</template>

<script>
    export default {
        name: "TagInput",
        data() {
            return {
                createForm: {
                    color: "",
                    name: "",
                },
                visible: false,
                is_create: false,
                circle: [
                    '#1b9aee',
                    '#dd6161',
                    '#5daf34',
                    '#e6a23c',
                    '#82848a',
                ],
                checkIndex: 0,
                checkArray: [],
                search: ""
            }
        }, watch: {
            value: {
                immediate: true,
                handler(val) {
                    this.checkArray = val
                }
            },
            visible: {
                immediate: true,
                handler(val) {
                    if (val === false) {
                        this.is_create = val;
                    }
                }
            }
        },
        computed: {
            showArray: {
                get: function () {
                    let showArray = this.data;
                    if (this.data.length === 0) {
                        return [];
                    }
                    let value = this.value || []

                    for (let item of showArray) {
                        if (value.length === 0) continue;
                        this.$set(item, 'is_check', false)
                        for (let v of value) {
                            if (v[this.prop] === item[this.prop]) {
                                this.$set(item, 'is_check', true)
                            }
                        }
                    }

                    return showArray
                },
                set: function (newValue) {

                }
            }
        },
        props: {
            prop: {
                type: String
            },
            data: {
                type: [Array],
                default: () => []
            },
            value: {
                type: [Array],
                default: () => []
            }
        },
        created() {

        },
        model: {
            prop: 'value',
            event: 'select'
        },
        methods: {
            tagColor(color) {
                let nc = color.replace("#", "")
                return {
                    'background-color': `#${nc}33`,
                    color: color,
                }
            },
            changedTag() {
                if (this.search === '') {
                    return
                }
                let show = this.showArray
                this.showArray = []

                for (let i = 0; i < show.length; i++) {
                    if (show[i][this.prop].indexOf(this.search) > -1) {
                        this.showArray.push(show[i])
                    }
                }
            },
            createTag() {
                this.$emit('created', this.createForm)
                this.is_create = false
            },
            selected(i) {
                this.showArray[i].is_check = !this.showArray[i].is_check;
                this.checkArray = []
                for (let item of this.showArray) {
                    if (item.is_check) {
                        this.checkArray.push(item)
                    }
                }

                this.$emit('change', {item: this.showArray[i], action: this.showArray[i].is_check})
                this.$emit('select', this.checkArray)
            },
            removeTag(index) {
                this.$emit('change', {item: this.checkArray[index], action: false})
                this.checkArray.splice(index, 1)
            },
            checkCircle(i) {
                this.checkIndex = i
                this.createForm.color = this.circle[i];
                this.$emit('checkColor', this.circle[i])
            }
        }
    }
</script>

