<template>
    <div class="add-from-page">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">{{$t('home')}}</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name:'ProjectOverview',params:{'id':$route.params.id} }">{{$t('project')}}
            </el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name:'Item',params:{'id':$route.params.id} }">Translation Item
            </el-breadcrumb-item>
            <el-breadcrumb-item>通过页面添加</el-breadcrumb-item>
        </el-breadcrumb>
        <br>

        <el-card shadow="never">
            <el-form :model="form" label-width="120px">
                <el-row type="flex" :gutter="15">
                    <el-col :span="8" style="text-align: center">
                        <div v-if="form.file_path !== ''">
                            <el-image class="jietu" :src="form.file_path">
                                <div slot="placeholder" class="image-slot">
                                    加载中<span class="dot">...</span>
                                </div>
                            </el-image>
                        </div>
                        <div v-else class="jietu">
                            <el-image :src="require('../../assets/gujia.png')"></el-image>
                        </div>
                    </el-col>
                    <el-col :span="16">
                        <el-form-item label="图片地址">
                            <el-input v-model="form.source_path"
                                      @input="blurHandle"
                                      placeholder="可复制图片，图片地址，Figma地址">
                                <i v-if="blurLoading" slot="suffix" class="el-icon-loading"></i>
                            </el-input>
                            <div style="color: #777777;font-size: 12px;line-height: 1.65">
                                <div>支持以下三种内容:</div>
                                <div>1. 剪切板的截图</div>
                                <div>2. http/https 协议图片地址</div>
                                <div>3. Figma
                                    设计稿地址,例如:https://www.figma.com/file/38BZdoPXQdKxIRwPqLTUBo/Prototype-v0.1?node-id=262%3A97
                                </div>
                            </div>

                        </el-form-item>
                        <el-form-item label="页面说明">
                            <el-input v-model="form.title"></el-input>
                        </el-form-item>
                        <el-form-item :label="`翻译项(${i + 1})`" v-for="(item,i) of form.items" :key="item.key">
                            <el-row type="flex" :gutter="10">
                                <el-col :span="6">
                                    <el-input v-model="form.key" placeholder="key"></el-input>
                                </el-col>
                                <el-col :span="6">
                                    <el-input v-model="form.value" placeholder="value"></el-input>
                                </el-col>
                                <el-col :span="6">
                                    <tag-input
                                            v-model="form.items.tags"
                                            :data="projectTags"
                                            @created="createdHandle"
                                            prop="name"></tag-input>
                                </el-col>
                                <el-col :span="6" style="text-align: right">
                                    <el-link type="primary" @click="addItem(i)">添加</el-link>
                                    <el-divider direction="vertical"></el-divider>
                                    <el-link type="danger"
                                             :disabled="form.items.length === 1"
                                             @click="deleteItem(i)">删除
                                    </el-link>
                                </el-col>
                            </el-row>
                        </el-form-item>
                    </el-col>

                </el-row>

            </el-form>
        </el-card>
    </div>
</template>

<script>
    import TagInput from "../../components/TagInput";
    import axios from "axios";

    export default {
        name: "AddFromPage",
        components:{
            TagInput
        },
        data() {
            return {
                blurLoading: false,
                form: {
                    file_path: "",
                    source_path: "",
                    title: "",
                    items: [
                        {
                            key: "",
                            value: "",
                            tags: [],
                        }
                    ]
                },
                projectTags: [],
            }
        },
        methods: {
            createdHandle(form) {
                axios.post(`/api/v1/projects/${this.$route.params.id}/tags`, form).then(() => {
                    this.getProjectTags();
                })
            },
            async getProjectTags() {
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/tags`)
                this.projectTags = data.data;
            },
            async blurHandle() {
                this.blurLoading = true
                let {data} = await axios.post(`/api/v1/urls/check`, {url: this.form.source_path})
                this.form.file_path = data.data.image || ""
                this.blurLoading = false
            },
            addItem(i) {
                this.form.items.splice(i, 0, {
                    key: "",
                    value: "",
                    tags: [],
                })
            },
            deleteItem(i) {
                if (this.form.items.length <= 1) {
                    return
                }
                this.form.items.splice(i, 1)
            },
        }
    }
</script>

<style lang="scss" scoped>
    .jietu {
        background: #f3f3f3;
        padding: 10px;
        border-radius: 11px;
    }
</style>