<template>
  <div class="item">
    <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
      <el-breadcrumb-item :to="{ path: '/' }">{{ $t('home') }}</el-breadcrumb-item>
      <el-breadcrumb-item :to="{ name:'ProjectOverview',params:{'id':$route.params.id} }">{{ $t('project') }}
      </el-breadcrumb-item>
      <el-breadcrumb-item>Translation Item</el-breadcrumb-item>
    </el-breadcrumb>

    <br>
    <el-card shadow="never">
      <el-row type="flex" justify="space-between">
        <el-col :span="12">
          <el-button
              type="primary"
              @click="addFormVisible = true"
              icon="el-icon-circle-plus-outline">{{ $t('add') }}
          </el-button>
          <!--                    <el-button-->
          <!--                               @click="$router.push({name:'AddFromPage',params:{id:$route.params.id}})"-->
          <!--                               type="primary">通过页面添加-->
          <!--                    </el-button>-->
          <el-button type="info"
                     plain
                     @click="$router.push({name:'ItemImport',params:{id:$route.params.id}})">{{ $t('import') }}
          </el-button>
        </el-col>
        <el-col :span="12" style="text-align: right;">
          <el-form inline style="align-items: center;display: flex;justify-content: flex-end;">
            <el-form-item>
              <el-input placeholder="Search..." v-model="searchVal" clearable class="input-with-select">
                <el-button slot="append" icon="el-icon-search" @click="searchHandle"></el-button>
              </el-input>
            </el-form-item>
            <el-form-item style="margin-right: 0">
              <el-button
                  type="info"
                  icon="el-icon-refresh"
                  @click="getItems({page: currentPage})">{{ $t('refresh') }}
              </el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>

      <vxe-table
          ref="xTable"
          :expand-config="{accordion: true}"
          resizable
          :data="tableData"
          style="width: 100%"
          :edit-config="{trigger: 'click', mode: 'cell'}"
          @edit-actived="editActivedEvent"
          @edit-closed="editClosedEvent">
        <vxe-table-column type="seq" width="60"></vxe-table-column>
        <vxe-table-column
            field="key"
            title="KEY"
            :edit-render="{name: 'input', defaultValue: ''}">
        </vxe-table-column>
        <vxe-table-column title="Description" type="expand">
          <template v-slot="{row,rowIndex}">
            <el-button
                @click="$refs.xTable.toggleRowExpansion(row)"
                type="text">[{{ $t('click_to_expand') }}]
            </el-button>
          </template>
          <template v-slot:content="{ row, rowIndex }">
            <div style="display: flex;">
              <div style="flex: 1;margin-right: 10px">
                <div v-if="!row.description" style="color: #aaa">{{ $t('no_description_provided') }}</div>
                <div v-if="editDescIndex!== rowIndex" v-html="row.description"></div>
                <div v-if="editDescIndex=== rowIndex">
                  <wang-editor v-model="row.description"></wang-editor>
                  <br>
                  <div style="text-align: right">
                    <el-button
                        v-if="editDescIndex=== rowIndex"
                        type="primary"

                        @click="saveDesc(row,rowIndex)">{{ $t('save') }}
                    </el-button>
                    <el-button
                        type="info"
                        plain
                        @click="editDescIndex = -1">{{ $t('cancel') }}
                    </el-button>
                  </div>
                </div>
                <el-image
                    v-if="row.file_path !== ''"
                    style=" width: 80px"
                    lazy
                    fit="cover"
                    :src="row.host + row.file_path"
                    :preview-src-list="[row.host + row.file_path]">
                </el-image>
              </div>
              <div v-if="editDescIndex!== rowIndex">
                <el-button
                    type="primary"
                    plain
                    @click="editDescIndex = rowIndex">{{ $t('edit') }}
                </el-button>

              </div>
            </div>
          </template>
        </vxe-table-column>
        <vxe-table-column
            field="tags" title="Tags">
          <template v-slot="{row,rowIndex}">
            <tag-input
                v-model="row.tags"
                :data="projectTags"
                @change="({item,action})=>changeTagHandle(row,{item,action})"
                @created="createdHandle"
                prop="name"></tag-input>
          </template>
        </vxe-table-column>
        <vxe-table-column
            field="value"
            :title="`Value(default:${firstLanguage.name})`"
            :edit-render="{name: 'input', defaultValue: ''}">
        </vxe-table-column>
        <vxe-table-column
            :title="$t('operation')" width="100">
          <template v-slot="{ row, rowIndex }">
            <vxe-button size="mini" status="danger"
                        @click="deleteHandle(row.project_id,row.project_language_id,row.identifier_id)">
              {{ $t('del') }}
            </vxe-button>
          </template>
        </vxe-table-column>
      </vxe-table>
      <br>
      <el-pagination
          background
          layout="prev, pager, next"
          :page-size="30"
          :current-page="currentPage"
          @current-change="currentChangeHandle"
          :total="count">
      </el-pagination>
    </el-card>

    <el-dialog
        :title="$t('create_translate_item')"
        :visible.sync="addFormVisible"
        width="40%">
      <el-form :model="addForm" :rules="addRules" ref="addForm" label-width="100px" class="demo-addForm">
        <el-form-item :label="$t('default_add_lang')" prop="region">
          <span>简体中文</span>
          <input type="hidden" v-model="addForm.language_code" value="zh-CN">
        </el-form-item>
        <el-form-item label="KEY" prop="key">
          <el-input v-model="addForm.key" :placeholder="$t('qing_shu_ru_key_ming__bu_nai_c')">
            <el-button slot="append" icon="el-icon-refresh" @click="createKeyName"></el-button>
          </el-input>
        </el-form-item>
        <el-form-item label="VALUE" prop="value">
          <el-input v-model="addForm.value" :placeholder="$t('qing_shu_ru_zhi')"></el-input>
        </el-form-item>
        <el-form-item :label="$t('tag')" prop="tags">
          <tag-input
              v-model="addForm.tags"
              :data="projectTags"
              @created="createdHandle"
              prop="name"></tag-input>
        </el-form-item>
        <el-form-item :label="$t('item_desc')" prop="description">
          <wang-editor v-model="addForm.description"></wang-editor>
        </el-form-item>

      </el-form>
      <div slot="footer" class="dialog-footer">
        <el-button plain type="primary" @click="addHandle(true)">{{ $t('finish_and_create_next') }}</el-button>
        <el-button type="primary" @click="addHandle(false)">{{ $t('finish') }}</el-button>
      </div>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
import WangEditor from "../../components/WangEditor";
import pinyin from "../../lib/pinyin";
import TagInput from "../../components/TagInput";
import {MessageBox} from "element-ui";

export default {
  name: "Item",
  components: {
    WangEditor,
    TagInput,
  },
  data() {
    let validatorCheckKey = (rule, value, callback) => {
      this.checkKey(value).catch(err => {
        callback(new Error(err.response.data.error))
      }).then(() => {
        callback();
      })
    }
    return {
      editDescIndex: -1,
      addFormVisible: false,
      addForm: {
        tags: [
          // {tag_id:0,name:''}
        ],
        project_id: 0,
        project_language_id: 0,
        language_code: '',
        identifier_id: 0,
        key: "",
        value: "",
        description: "",
        file_path: "",
      },
      addRules: {
        key: [
          {required: true, message: this.$t('duplicate_key'), trigger: 'blur'},
          {validator: validatorCheckKey, trigger: 'blur'}
        ],
        value: [
          {required: true, message: this.$t('please_enter_value'), trigger: 'blur'},
        ]
      },
      tableData: [],
      currentPage: 1,
      count: 0,
      projectTags: [
        {tag_id: 1, name: 'API', color: 'red'},
        {tag_id: 2, name: 'iOS', color: 'blue'},
      ],
      firstLanguage: {
        name: "简体中文",
        code: "zh-CN"
      },
      searchVal: "",
      project: {
        project_id: 0,
        name: '',
        description: '',
        default_code: '',
        founder: '',
        founder_id: '',
        created_at: '',
        updated_at: '',
      }
    }
  },
  computed: {
    tags() {
      let tags = []
      for (let item of this.projectTags) {
        tags.push(item.name)
      }
      return tags
    },
    noPic() {
      return require("../../assets/no_pic.png")
    }
  },
  mounted() {
    this.getItems({page: this.currentPage});
    this.getProjectTags();
    this.fetchProject()
  },
  created() {
    this.addForm.language_code = this.firstLanguage.code
    this.addForm.project_id = parseInt(this.$route.params.id);
    this.currentPage = parseInt(this.$route.query.page || 1)
  },
  methods: {
    async fetchProject() {
      let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}`)
      this.project = data.data;
      this.firstLanguage.code = this.project.default_code
      await this.fetchLanguage(this.firstLanguage.code)
    },
    async fetchLanguage(code) {
      let {data} = await axios.get(`/api/v1/languages`)
      for (let item of data) {
        if (code === item.code) {
          this.firstLanguage.name = item.language
        }
      }
    },
    saveDesc(row, rowIndex) {
      this.editClosedEvent({row})
      this.editDescIndex = -1
    },
    createdHandle(form) {
      axios.post(`/api/v1/projects/${this.$route.params.id}/tags`, form).then(() => {
        this.getProjectTags();
      })
    },
    changeTagHandle(row, {item, action}) {
      if (action) {
        axios.post(`/api/v1/projects/${this.$route.params.id}/key/${row.identifier_id}/tags/${item.tag_id}/relation`)
      } else {

        axios.delete(`/api/v1/projects/${this.$route.params.id}/key/${row.identifier_id}/tags/${item.tag_id}/relation`)
      }
    },
    deleteHandle(project_id, project_language_id, identifier_id) {
      if (typeof identifier_id === 'undefined') {
        this.getContent({page: this.currentPage});
        return
      }
      MessageBox.confirm(this.$t('del_key_tips'), '⚠️', {
        confirmButtonText: this.$t('ok'),
        cancelButtonText: this.$t('cancel'),
        type: 'warning'
      }).then(() => {
        this.deleteKey(identifier_id)
      }).catch(() => {

      });
    },
    createKeyName() {
      if (this.addForm.value.trim().length > 0) {
        let v = this.addForm.value.trim()
        if (/\p{Unified_Ideograph}/u.test(v)) {
          v = pinyin.pinyin(v)
        }
        v = v.replace(/(^\s+)|(\s+$)|\s+/g, '_')
        v = v.replace("-", "_")
        v = v.replace(/[^\w]/g, "")
        v = v.toLowerCase()

        //如果长度为0，生成随机
        if (v.length < 5) {
          v = 'key_' + Math.random().toString(36).substr(2)
        }

        this.addForm.key = v.substr(0, 30)
        return true
      }
      //生成随机字符串
    },
    showDesc(str, len = 15) {
      let s = this.delHtmlTag(str).substring(0, len)
      if (s.length >= len) s += "..."
      return s + `[${this.$t('view_more')}]`
    },
    delHtmlTag(str) {
      return str.replace(/<[^>]+>/g, "");
    },
    addHandle(isCreateNext = false) {

      this.$refs["addForm"].validate((valid) => {
        if (!valid) {
          return false;
        }

        let form = JSON.parse(JSON.stringify(this.addForm))

        axios.post(`/api/v1/projects/${this.$route.params.id}/keys/contents`, form).then(() => {
          if (isCreateNext) {
            // this.clearAddForm()
            this.$refs["addForm"].resetFields();
            this.addForm.tags = [];
            this.getProjectTags();
            return
          }
          this.addFormVisible = false
          this.getItems()
        })
      });

    },
    clearAddForm() {
      this.addForm = {
        tags: [
          // {tag_id:0,name:''}
        ],
        project_id: 0,
        project_language_id: 0,
        language_code: 0,
        identifier_id: 0,
        key: "",
        value: "",
        description: "",
        file_path: "",
      }
    },
    changeProjectTags(val) {
      for (let item of val) {
        if (this.tags.indexOf(item) > -1) {
          continue
        }

        axios.post(`/api/v1/projects/${this.$route.params.id}/tags`, {name: item})
      }
    },

    async checkKey(name) {
      await axios.get(`/api/v1/projects/${this.$route.params.id}/keys/search`, {params: {s: name}})
    },
    async getProjectTags() {
      let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/tags`)
      this.projectTags = data.data;
    },
    searchHandle() {
      this.getItems({'search': this.searchVal})
    },
    currentChangeHandle(page) {
      this.currentPage = page;
      this.$router.push({query: {page: page}});
      this.getItems({page: page})
    },
    editActivedEvent({row, column}, event) {
      //console.log(`打开 ${column.title} 列编辑`)
    },
    editClosedEvent({row}) {
      let params = {
        project_id: parseInt(this.$route.params.id),
        project_language_id: row.project_language_id || null,
        language_code: row.language_code || 'zh-CN',
        identifier_id: row.identifier_id || null,
        key: row.key,
        value: row.value,
        description: row.description,
        file_path: row.file_path,
      }
      if (params.identifier_id == null) {
        if (row.key === "") return;
        this.saveKeyContent(row, params);
        return
      }
      this.updateKeyContent(row.identifier_id, params)
    },
    updateTags(val, row) {
      console.log(val, row)
      for (let item of val) {
        if (this.tags.indexOf(item) > -1) {
          continue
        }
        //TODO 需要写一个类似于tb的tag组件才能满足
        axios.post(`/api/v1/projects/${this.$route.params.id}/tags`, {name: item})
      }

      // axios.post(`/api/v1/projects/${this.$route.params.id}/tags`, {name: item})

    },
    updateKeyContent(identifier_id, params) {
      axios.put(`/api/v1/projects/${this.$route.params.id}/keys/${identifier_id}/contents`, params).then((data) => {

      })
    },
    deleteKey(identifier_id) {
      axios.delete(`/api/v1/projects/${this.$route.params.id}/keys/${identifier_id}/contents`).then(() => {
        this.getItems({'search': this.searchVal})
      })
    },

    saveKeyContent(row, params = {}) {
      axios.post(`/api/v1/projects/${this.$route.params.id}/keys/contents`, params).then(({data}) => {
        this.$set(row, 'identifier_id', data.data.identifier_id)
      })
    },
    async getItems(params = {}) {
      let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/translations/contents`, {params: params});
      this.tableData = data.data.data;
      for (let item of this.tableData) {
        if (item.tags === null) {
          item.tags = []
        }
      }
      this.count = data.data.paging.count
    },
  }
}
</script>

<style lang="scss" scoped>
.el-popover {
  .desc {
    img {
      max-width: 100% !important;
    }
  }
}


</style>