<template>
    <div class="content">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">{{$t('home')}}</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ path: '/project' }">{{$t('project')}}</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name:'ProjectOverview',params:{id:$route.params.id} }"> {{$t('overview')}}
            </el-breadcrumb-item>
            <el-breadcrumb-item> {{$t('translation')}}</el-breadcrumb-item>
        </el-breadcrumb>
        <br>


        <el-card shadow="never">
            <el-alert
                    :title="`Current translation language: ${translate_code}`"
                    type="success"
                    close-text="OK">
            </el-alert>
            <br>

            <el-row type="flex" justify="space-between">
                <el-col :span="12">
                    <vxe-select v-model="currentLanguage" placeholder="参照语言" @change="changeHandle">
                        <vxe-option v-for="language in languages" :key="language.language_id" :value="language.code"
                                    :label="language.language"/>
                    </vxe-select>

                    <el-checkbox style="margin-left: 5px"
                                 true-label="un_translated"
                                 false-label="all"
                                 v-model="showUnTranslate" @change="showUnTranslateHandle">Show
                        untranslated content?
                    </el-checkbox>
                </el-col>
                <el-col :span="6">
                    <el-input v-model="filterName"
                              type="search"
                              placeholder="Search...">
                        <el-button slot="append"
                                   icon="el-icon-search"
                                   @click="getTranslations(currentLanguage, currentPage, showUnTranslate)"></el-button>
                    </el-input>
                </el-col>
            </el-row>

            <vxe-table
                    :expand-config="{accordion: true}"
                    resizable
                    :row-class-name="rowClassName"
                    :loading="loading"
                    class="my-table"
                    keep-source
                    highlight-cell
                    show-overflow
                    :edit-config="{trigger: 'click', mode: 'cell',icon: 'fa fa-pencil'}"
                    @edit-closed="editClosedEvent"
                    :data="list"
                    border=inner
                    ref="xTable">
                <vxe-table-column type="seq" width="60" :label="$t('id')"></vxe-table-column>
                <vxe-table-column
                        field="key"
                        type="html"
                        title="KEY">
                </vxe-table-column>

                <vxe-table-column
                        field="description"
                        type="expand"
                        title="Description">
                    <template v-slot="{row,rowIndex}">
                        <el-button
                                @click="$refs.xTable.toggleRowExpansion(row)"
                                type="text">[Click to expand]
                        </el-button>
                    </template>
                    <template v-slot:content="{ row, rowIndex }">
                        <div style="display: flex;">
                            <div style="flex: 1;margin-right: 10px">
                                <div v-if="!row.description" style="color: #aaa">No description provided.</div>
                                <div v-else v-html="row.description"></div>
                                <el-image
                                        v-if="row.file_path !== ''"
                                        style=" width: 80px"
                                        lazy
                                        fit="cover"
                                        :src="row.host + row.file_path"
                                        :preview-src-list="[row.host + row.file_path]">
                                </el-image>
                            </div>

                        </div>
                    </template>
                </vxe-table-column>
                <vxe-table-column
                        type="html"
                        field="from_value"
                        :title="$t('ref_language')">
                </vxe-table-column>
                <vxe-table-column
                        field="to_value"
                        title="Value(You can edit this column)"
                        :edit-render="{name: '$input', props: {type: 'textarea'},autoselect: true}">
                </vxe-table-column>
            </vxe-table
            >
            <br>
            <el-pagination
                    background
                    layout="prev, pager, next"
                    @current-change="currentChangeHandle"
                    :current-page="currentPage"
                    :page-size="50"
                    :total="count">
            </el-pagination>
        </el-card>
    </div>
</template>

<script>
    import axios from 'axios'
    import {Message} from 'element-ui'
    import XEUtils from 'xe-utils'

    export default {
        name: "Content",

        data() {
            return {
                filterName: '',
                tableData: [],
                currentLanguage: 'zh-CN',
                showUnTranslate: 'all',
                languages: [],
                loading: false,
                count: 0,
                currentPage: 1,
            }
        },
        computed: {
            translate_code() {
                for (let item of this.languages) {
                    if (this.$route.params.code === item.code) {
                        return item.language;
                    }
                }
            },
            list() {
                const filterName = XEUtils.toString(this.filterName).trim().toLowerCase();
                if (filterName) {
                    const filterRE = new RegExp(filterName, 'gi');
                    const searchProps = ['key', 'from_value'];
                    // const rest = this.tableData.filter(item => searchProps.some(key => XEUtils.toString(item[key]).toLowerCase().indexOf(filterName) > -1));
                    return this.tableData.map(row => {
                        const item = Object.assign({}, row);
                        searchProps.forEach(key => {
                            item[key] = XEUtils.toString(item[key]).replace(filterRE, match => `<span class="keyword-lighten">${match}</span>`)
                        });
                        return item
                    })
                }
                return this.tableData
            }
        },
        created() {
            this.getTranslations(this.currentLanguage, this.currentPage);
            this.fetchLanguage();
            this.currentPage = this.$route.query.page || 1;
        },
        methods: {
            showUnTranslateHandle(val) {
                this.getTranslations(this.currentLanguage, this.currentPage, val)
            },
            rowClassName({row, rowIndex}) {

                if (row.to_value !== "") {
                    return "row-green"
                }
            },
            currentChangeHandle(page) {
                this.currentPage = page;
                this.$router.push({query: {page: page}});
                this.getTranslations(this.currentLanguage, this.currentPage, this.showUnTranslate)
            },
            editClosedEvent({row, column}) {
                let xTable = this.$refs.xTable;
                let field = column.property;
                let cellValue = row[field];
                // 判断单元格值是否被修改
                if (xTable.isUpdateByRow(row, field)) {
                    setTimeout(() => {
                        //alert("局部保存成功")
                        axios.post(`/api/v1/projects/${this.$route.params.id}/translations/${this.$route.params.code}/contents`, {
                            identifier_id: row.identifier_id,
                            project_id: Number(this.$route.params.id),
                            value: row.to_value,
                        });
                        // 局部更新单元格为已保存状态
                        this.$refs.xTable.reloadRow(row, null, field)
                    }, 300)
                }
            },


            changeHandle() {
                this.getTranslations(this.currentLanguage, this.currentPage, this.showUnTranslate)
            },
            async fetchLanguage() {
                let {data} = await axios.get("/api/v1/languages");
                this.languages = data
            },
            nameChangeEvent({row, rowIndex, column, columnIndex}, event) {
                let val = event.target.value;
                if (row.identifier_id) return;
                setTimeout(() => {

                    axios.get(`/api/v1/projects/${this.$route.params.id}/keys/search`, {params: {s: val}})
                        .then(() => {
                        })
                        .catch(error => {
                            Message.error(error.response.data.error);
                        })
                }, 300)
            },
            async getTranslations(code = 'zh-CN', page = 1, type = 'all') {
                this.loading = true;
                let {data} = await axios.get(`/api/v1/projects/${this.$route.params.id}/translations`, {
                    params: {
                        from_code: code,
                        to_code: this.$route.params.code,
                        page: page,
                        type: type,
                        search: this.filterName ?? "",
                    }
                });
                this.tableData = data.data.data;
                this.count = data.data.paging.count;
                this.loading = false;
            },
            async insertEvent(row = 1) {
                let record = {
                    key: '',
                    value: ""
                };
                let {row: newRow} = await this.$refs.xTable.insertAt(record, row);
                await this.$refs.xTable.setActiveCell(newRow, 'key')
            },
            saveEvent() {
                const {insertRecords, removeRecords, updateRecords} = this.$refs.xTable.getRecordset();
                let data = {
                    project_id: Number(this.$route.params.id),
                    language_code: "zh-CN",
                    insert: insertRecords,
                    remove: removeRecords,
                    update: updateRecords,
                };
                axios.put(`/api/v1/projects/${this.$route.params.id}/batch-translations`, data)
                    .then(() => {
                        this.$router.replace({name: "Project"})
                    })
            }
        }
    }
</script>
<style lang="scss">
    .keyword-lighten {
        color: #000;
        background-color: #FFFF00;
    }

    .my-table .vxe-body--row.row-green {
        background-color: rgba(52, 175, 147, 0.07) !important;
    }
</style>