<template>
    <div class="add-project">
        <el-breadcrumb class="breadcrumb" separator-class="el-icon-arrow-right">
            <el-breadcrumb-item :to="{ path: '/' }">Home</el-breadcrumb-item>
            <el-breadcrumb-item :to="{ name:'Project',params:{'id':$route.params.id} }">Project Management
            </el-breadcrumb-item>
            <el-breadcrumb-item> 添加项目</el-breadcrumb-item>
        </el-breadcrumb>
        <br>

        <el-card shadow="never">
            <el-form ref="form" :model="form" :rules="rules" label-width="220px">
                <el-form-item label="Project name" prop="name">
                    <el-input v-model="form.name"/>
                    <span style="color: #a0a3a8;font-size: 13px">名称最好使用项目名（使用英文、数字与下划线即 [a-zA-Z0-9_] ），请不要带空格或者特殊字符，方便后续使用命令行生成项目翻译文件。</span>
                </el-form-item>

                <el-form-item label="项目描述">
                    <el-input type="textarea" placeholder="项目描述" v-model="form.description"/>
                </el-form-item>
                <el-form-item label="翻译语言" prop="languages">
                    <el-select v-model="form.languages" multiple clearable style="width: 100%">
                        <el-option v-for="(language) of languages"
                                   :label="`${language.language} (${language.code})`"
                                   :value="language.code"
                                   :key="language.language_id"/>
                    </el-select>
                    <span style="color: #a0a3a8;font-size: 13px">请勾选项目所需要支持的语言。</span>
                    <span style="color: #c93838">请注意：简体中文必须勾选，因为目前添加翻译条目时，默认的是简体中文</span>
                </el-form-item>
                <el-form-item label="默认语言">
                    <el-select v-model="form.default_code" clearable style="width: 100%">
                        <el-option
                                :value="language"
                                :key="language"
                                v-for="language of form.languages"
                        ></el-option>
                    </el-select>
                    <span style="color: #a0a3a8;font-size: 13px">如果不选择，默认会取翻译语言中的第一项</span>
                </el-form-item>
                <el-form-item>
                    <el-button type="primary" @click="onSubmit">立即创建</el-button>
                    <el-button @click="$router.back()">取消</el-button>
                </el-form-item>
            </el-form>
        </el-card>
    </div>
</template>

<script>
    import axios from 'axios'

    export default {
        name: "AddProject",
        data() {

            let validateName = (rule, value, callback) => {
                let reg = /^[a-zA-Z0-9_]{3,50}$/
                let re = new RegExp(reg);
                console.log(re.test(value))
                if (!re.test(value)) {
                    callback(new Error('请输入规范的项目名称'));
                    return
                }
                callback();
            };

            return {
                form: {
                    name: '',
                    description: '',
                    languages: [],
                    default_code: ''
                },
                languages: [],
                rules: {
                    name: [
                        {required: true, message: 'Please enter a project name', trigger: 'blur'},
                        {min: 3, max: 50, message: 'Please enter 3 to 50 characters', trigger: 'blur'},
                        {validator: validateName, trigger: 'blur' }
                    ],
                    languages: [
                        {
                            type: 'array',
                            required: true,
                            message: 'Please select the language you want to translate.',
                            trigger: 'change'
                        }
                    ],
                }
            }
        },
        created() {
            this.getLanguage()
        },
        methods: {
            onSubmit() {
                this.$refs['form'].validate((valid) => {
                    if (!valid) {
                        return false;
                    }

                    if (this.form.default_code === '') {
                        this.form.default_code = this.form.languages[0]
                    }

                    axios.post('/api/v1/projects', this.form).then(() => {
                        this.$router.replace({name: "Project"})
                    })
                });

            },
            async getLanguage() {
                let {data} = await axios.get('/api/v1/languages');
                this.languages = data
            }

        }
    }
</script>

